div {
    box-sizing: border-box;
}

body {
    margin: 0px;
}

.companyname {
    margin-right: 100px;
    text-align: center;
}

.menu {
    margin-right: 30px;
}

#main {
    background-color: black;
    height: 1000px;
    background-image: url(cryptocurrency-3085139_1280.jpeg);
    background-size: cover;
    background-position: center;
    background-attachment: scroll;
}
.blank {
    height: 300px;
}
.main-title {
    color: white;
    font-size: 40px;
    border-top: 400px;
    margin-left: 50px;
    box-sizing: border-box;
}

.main-title:hover {
    color: rgb(220, 185, 185);
}

#member {
    background-color: lavender;
    max-height: 1000px;
    text-align: center;
    font-size: xx-large;
}

#project {
    background-color: lightblue;
    height: 840px;
    text-align: center;
    font-size: xx-large;
}

.member {
    margin: auto;
}

.jihyun {
    width: 100%;
    height: 300px;
    background-color: rgb(208, 208, 218);
    font-size: x-large;
    color: rgb(0, 0, 0);
    border: 5px dotted burlywood;
    border-radius: 15px;
    float: left;
    margin-bottom: 10px;
}

.eunjae {
    width: 100%;
    height: 300px;
    margin-bottom: 10px;
    background-color: rgb(131, 122, 122);
    font-size: x-large;
    color: rgb(0, 0, 0);
    float: left;
    border: 5px dotted burlywood;
    border-radius: 15px;
}

.project {
    height: 775px;
    width: 50%;
    display: inline-block;
    line-height: left;
}

.coin {
    background-color: olive;
    font-size: large;
    color: white;
}

.bot {
    background-color: rgb(128, 48, 48);
    font-size: large;
    color: white;
}

.person_image {
    display: flex;
    width: 30%;
    height: 50%;
    display: block;
    margin-top: 10px;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 20px 20px rgb(70, 3, 3);
}

footer {
    background-color: palevioletred;
    text-align: center;
    font-size: x-large;
    margin-top: 10px;
    margin-bottom: 20px;
}
